<template>
  <div class="card">
    <div class="card-header">
      Exam02Watch
    </div>
    <div class="card-body">
      <div class="form-group row">
          <label class="col-sm-2 col-form-label">UserId</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="userId">
          </div>
      </div>
      <hr/>
      <form>      
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Name</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="product.name">
          </div>
        </div>
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Company</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" v-model="product.company">
          </div>
        </div>
        <div class="form-group row">
          <label class="col-sm-2 col-form-label">Price</label>
          <div class="col-sm-10">
            <input type="number" class="form-control" v-model.number="product.price">
          </div>
        </div>
      </form> 
      <hr/>
      <button @click="handleButton" class="btn btn-info btn-sm">product 객체 변경</button>     
    </div>
  </div>
</template>

<script setup>
import { ref, watch, toRaw } from "vue";

const userId = ref("");
const product = ref({
  name: "",
  company: "",
  price: 0
});

//객체 자체 변경
function handleButton() {
  product.value = {
    name: "",
    company: "",
    price: 0
  };
}

//감시 소스가 값일 경우
watch(userId, 
  (newUserId, oldUserId) => {
    console.group("userId 변경 감시");
    console.log("newUserId:", newUserId);
    console.log("oldUserId:", oldUserId);
    console.groupEnd();
  }
);

//감시 소스가 객체일 경우
//자체가 변경되었을 경우: new와 old의 참조 객체는 다름
//속성값이 변경되었을 경우: new와 old의 참조 객체는 동일
watch(product, 
  (newProduct, oldProduct) => {
    console.group("product 객체 변경 감시");
    console.log("newUserId:", toRaw(newProduct));
    console.log("oldUserId:", toRaw(oldProduct));
    console.groupEnd();  
  },
  { deep: true } //속성 변경도 감시
);

//감시 소스가 객체의 속성일 경우
watch(() => product.value.name,
  (newName, oldName) => {
    console.group("product 객체의 name 속성 변경 감시")
    console.log("newName:", newName);
    console.log("oldName:", oldName);
    console.groupEnd();
  }
);

//감시 소스가 멀티인 경우
watch([userId, product], 
  ([newChild, newProduct], [oldChild, oldProduct]) => {
    console.group("child와 product 멀티 변경 감시")
    console.log("new:", [newChild, toRaw(newProduct)]);
    console.log("old:", [oldChild, toRaw(oldProduct)]);
    console.groupEnd();
  }, 
  { deep: true } //속성 변경도 감시
);
</script>

